import * as React from "react"
import { Link } from "gatsby"
import ThemeToggle from "./theme-toggle"

type TabPath = "/" | "/dairy" | "/memo";

const Navigation: React.FC = () => {
  const [activeTab, setActiveTab] = React.useState<TabPath>("/")

  React.useEffect(() => {
    if (typeof window !== "undefined") {
      const path = window.location.pathname as TabPath
      setActiveTab(path)
    }
  }, [])

  const getTabClassName = (path: TabPath): string => {
    const baseClasses = "relative px-4 py-4 transition-colors"
    const activeClasses = "text-x-blue"
    const inactiveClasses = "text-x-black dark:text-x-white hover:bg-x-extra-light-gray dark:hover:bg-x-dark-gray"
    
    return `${baseClasses} ${activeTab === path ? activeClasses : inactiveClasses}`
  }

  const renderActiveIndicator = (isActive: boolean): React.ReactNode => {
    return isActive && (
      <div className="absolute bottom-0 left-0 w-full h-1 bg-x-blue" />
    )
  }

  return (
    <nav className="fixed top-0 w-full bg-x-white dark:bg-x-black border-b border-x-extra-light-gray dark:border-x-dark-gray z-50">
      <div className="max-w-layout mx-auto">
        <div className="flex items-center justify-center h-14">
          <div className="flex space-x-1">
            <Link
              to="/"
              className={getTabClassName("/")}
              onClick={() => setActiveTab("/")}
              role="tab"
              aria-selected={activeTab === "/"}
              aria-controls="about-panel"
            >
              <span className="font-bold">About</span>
              {renderActiveIndicator(activeTab === "/")}
            </Link>
            <Link
              to="/dairy"
              className={getTabClassName("/dairy")}
              onClick={() => setActiveTab("/dairy")}
              role="tab"
              aria-selected={activeTab === "/dairy"}
              aria-controls="dairy-panel"
            >
              <span className="font-bold">Dairy</span>
              {renderActiveIndicator(activeTab === "/dairy")}
            </Link>
            <Link
              to="/memo"
              className={getTabClassName("/memo")}
              onClick={() => setActiveTab("/memo")}
              role="tab"
              aria-selected={activeTab === "/memo"}
              aria-controls="memo-panel"
            >
              <span className="font-bold">Memo</span>
              {renderActiveIndicator(activeTab === "/memo")}
            </Link>
          </div>
          <div className="absolute right-4">
            <ThemeToggle />
          </div>
        </div>
      </div>
    </nav>
  )
}

export default Navigation